body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    width: 68%;
    margin: auto;
    overflow-x: visible;
}
nav{
    display: flex;
    justify-content: space-between;
}
.left{
    display: flex;
}
ul{
    list-style-type: none;
    display: flex;
    margin-top: 26px;
}
ul li{
    margin-left: 20px;
}
.button-haut{
    font-size: 17px;
    height: 35px;
    width: 90px;
    margin-top: 18px;
    border:1px solid black;
    border-radius: 5px;
    background-color: white;
}
button:hover{
    background-color: black;
    color: white;
}
.compteur{
    color: white;
    border-radius: 100%;
    background-color: black;
    padding: 2px;
    padding-left: 7px;
    padding-right: 7px;

}
.caroussel{
    height: 300px;
    width: 100vw;
    position: relative;
    left: calc(-50vw +  50%);
    background-color: rgba(18, 19, 20, 0.904);
}   
.slide{
    text-align: center;
    margin-top: 40px;
}
.text1{
    color: white;
    font-size: 55px;
    font-weight: bold;
}
.text2{
    color: gray;
    font-size: 19px;
    margin-top: -35px;
}

.product-item {
    height: 370px;
    width: 265px;
    border: 1px solid black;
    border-radius: 10px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
    display: flex;
    flex-direction: column;
    margin-bottom: 90px;
    position: relative;
}

.item-cover {
    border-radius: 10px;
    height: 170px;
    width: 265px;
}

.titre {
    text-align: center;
    margin-top: 20px;
    font-size: 20px;
    font-weight: bold;
    color: rgba(20, 19, 19, 0.918);
}

.star {
    text-align: center;
    margin-top: 10px;
}

.str {
    margin-bottom: 15px;
}

.Price {
    text-align: center;
}

.barre {
    text-decoration: line-through;
    color: gray;
}

.button-bas {
    font-size: 17px;
    height: 35px;
    width: 110px;
    margin-top: 18px;
    border: 1px solid black;
    border-radius: 5px;
    background-color: white;
    margin: auto;
    margin-bottom: 30px;
}

.sale {
    position: absolute;
    text-align: center;
    top: 10px;
    right: 10px;
    width: 40px;
    height: 20px;
    background-color: rgba(18, 19, 20, 0.904);
    color: white;
    font-size: 14px;
    border-radius: 5px;
}
.shoping-list{
    width: 100%;
    margin-top: 100px;
}
.produit-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.footer{
    text-align: center;
    display: grid;
    align-items: center;
    color: white;
    position: relative;
    background-color: rgba(18, 19, 20, 0.904);
    width: 100vw;
    left: calc(-50vw +  50%);
    height: 100px;

}
